	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sprints</h2>\n<p>In Scrum, the <strong>Sprint</strong> is a timebox; a period of working time, usually between one and four weeks. The Delivery Team works on your project over consecutive Sprints.</p>\n<p>Your Sprints should remain the same length of time throughout the length of the project. </p>\n<p>The Sprint is usually kicked off each time with the Sprint Planning sessions, and ends with the Sprint Review and Retrospective meetings.</p>\n<p>Your team works to break up the User Stories to a size that can be completed within the duration of the Sprint without carrying over to the next. And having short sprints allows regular feedback loops between the Stakeholders on the output from the Delivery Team.</p>\n<p>“Sprint” and “Iteration” are often used interchangeably.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Agile Alliance on <a href='https://www.agilealliance.org/glossary/iteration/' target='_blank' rel='nofollow'>Iterations</a></li>\n<li>Agile Alliance on <a href='https://www.agilealliance.org/glossary/timebox/' target='_blank' rel='nofollow'>Timeboxes</a></li>\n<li>Agile Coach on <a href='http://agilecoach.typepad.com/agile-coaching/2014/02/sprint-vs-iteration.html' target='_blank' rel='nofollow'>Sprint vs Iteration</a></li>\n<li>Scrum Alliance <a href='https://www.scrumalliance.org/community/articles/2014/february/timeboxing-a-motivational-factor-for-scrum-teams' target='_blank' rel='nofollow'>Timeboxing as a Motivational Factor</a></li>\n<li>Scrum Alliance <a href='https://www.scrumalliance.org/community/articles/2014/may/sprint-is-more-than-just-a-timebox' target='_blank' rel='nofollow'>Sprint is More Than Just a Timebox</a></li>\n<li>Scrum Inc <a href='https://www.scruminc.com/what-is-timeboxing/' target='_blank' rel='nofollow'>What is Timeboxing</a></li>\n<li>Scrum.org <a href='https://www.scrum.org/resources/what-is-a-sprint-in-scrum' target='_blank' rel='nofollow'>What is a Sprint in Scrum</a></li>\n</ul>","fields":{"slug":"/agile/sprints/"},"frontmatter":{"title":"Sprints"}}},"pathContext":{"slug":"/agile/sprints/"}}

